@import url('https://fonts.googleapis.com/css2?family=Suravaram&display=swap');


.headerBody{
  width: 100%;
  height: 750px;
  font-family: 'Suravaram', serif;
  display: block;
}

.header {
    width: 100%;
    height: 100px;
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    position: fixed;
    z-index: 9999;
    color: white;
}
  
.header:hover{
  background-color: rgba(255, 255, 255, 0.634);
  color: black;
}

.active{
  width: 100%;
  position: fixed;
  /* display: inline-block; */
  display: flex;
    justify-content: space-between;
    align-items: center;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.879);
  color:black;
  box-shadow: 0 5px 5px -5px rgba(197, 197, 197, 0.879);
  margin: 0;
}

.nav{
    all:unset;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin: 10px;
}

.nav:link{text-decoration: none;}



.logo{
  font-family: 'Suravaram', serif;
  margin: 10px 13px;
  font-size: 35px;
  cursor: pointer;
}

.header-L{
  width: 60vw; 
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 35px;
}

.header-R {
  width: 40vw; 
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.category-L{
  padding: 0px 32px;
  font-size: 20px;
}

.category-R{
  padding-right: 90px;
  font-size: 15px;
}

.category-L>li,.category-R>li{
  margin: 10px;
  cursor: pointer;
}  

/*--공통적용--*/
.category{
  width: 100%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin: 5px;
}
/*------------*/


/*슬라이드*/
.slideImg{
  width: 100%;
  height: 750px;
  display: block;
  position: absolute;
  top: 0;
}

.imgList{
  width: 100%;
  height: 750px;
  list-style:none;
  margin: 0px;
  padding-left: 0px;
}

.mainImg{
  width: 100%;
  height: 750px;
  list-style:none;
  margin: 0px;
}

img{
  width:100%;
  height:100%;
  object-fit:cover;
}

