
.footerBody{
    width: 100%;
    height: 800px;
    margin-top: 60px;
 

    /* background-color: bisque; */
}

.footer{
    display: flex;
    flex-flow: column wrap;
    /* display: block; */
}

.footerContent1{
    width: 100%;
    height: 350px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    /* background-color: blue; */
}

.showroom{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column wrap;
    border-right: 0.7px solid #656565;
    /* background-color: olive; */
}

.showroom>p{
    font-family: 'Hahmlet', serif;
    margin: 0px 0px 10px 0px ;  
}

.showroom :nth-child(2){
    font-size: 5px;
}

.showroomImg{
    padding: 5px;

}

.showroomImg>img{
    width: 340px;
    height: 202px;
    margin: 5px;
}

.sns{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column wrap;
    /* background-color: darkorange; */
}

.sns>p{
    font-family: 'Hahmlet', serif;
    margin: 0px 0px 10px 0px ;  
}

.sns :nth-child(2){
    font-size: 10px;
}

.snsBox{
    width: 720px;
    height: 200px;
    padding: 5px;
    cursor: pointer;
}

.snsBox>a{
    all: unset;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
}

.snsimg{
    width: 100px;
    height: 100px;
    margin: 1px;
}

.footerContent2{
    width: 100%;
    margin-top: 50px;
    padding-top: 50px;
    border-top: 1px solid #656565;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    /* background-color: rgb(255, 0, 195); */
}

.footerMenu{
    height: 300px;
    margin: 0px 50px;
    display: flex;
    flex-flow: column wrap;
    align-items: stretch;
}

.f-menu-list{
    all: unset;
    display: flex;
    justify-content: flex-start;
    flex-flow: row nowrap;
}

.f-menu-list>li{
    all: unset;
    font-size: 11px;
    margin: 10px;
    font-family: 'Nanum Gothic', sans-serif;
    font-weight: 700;
    cursor: pointer;
}

.company{
    /* display: flex;
    flex-wrap: nowrap;
    justify-content: space-around; */
    margin-top: 50px;
}

.companyInfo{
    font-size: 12px;
    border-spacing: 10px;
    height: 100px;
    margin-top: 30px;
}

tr :nth-child(2n){
    padding-left: 50px;
}

.companyImg> img{
    width: 600px;
    height: 300px;
    
}