@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:wght@700&family=Hahmlet:wght@200&family=Nanum+Gothic&display=swap');


.mainBody{
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    /* background-color: rgb(255, 235, 59); */

}

.main-logo{
    width: 100%;
    height: 100px;
    top: 750px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column wrap;
    margin: 60px 0px;
}

.main-logo>p{
    margin: 5px;
    font-size: 13px;
    color: rgb(51, 51, 51);
}

.itemList{
    width: 100%;
}

.item-category{
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.categoryImg{
    width: 350px;
    height: 350px;
    position: relative;
    cursor: pointer;
    color: white;
}

.categoryImg:hover{
    transition: all ease-in-out 0.1s;
    transform: translateY(-10px);
    opacity: 0.6;
    color: black;
}

.categoryImg>img{
    opacity: 0.85;
}

.itemName{
    font-family: 'Crimson Text', serif;
    position: absolute;
    width: 100%;
    top: 45%;
    font-size: 30px;
    text-align: center;
    text-shadow: 2px 2px 6px gray;
}

.item-title{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column wrap;
    margin-top: 60px;
    color: rgb(98, 98, 98);
}

.item-title>p{
    margin: 2px 0px;
    font-family: 'Hahmlet', serif;
    font-weight: 500;
}

.item-title :nth-child(1){
    font-size: 16px;
}

.item-title :nth-child(2){
    font-size: 12px;
}

.itemBox{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    font-family: 'Nanum Gothic', sans-serif;
}

.item{
    width: 300px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px 10px;
    cursor: pointer;
}

.itemImg{
    width: 300px;
    height: 300px;
}

.detail{
    font-size: 13px;
    color: #656565;
}

.title{
    width: 300px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(179, 179, 179);
}

.content{
    margin: 10px 0px 5px 0px;
    color: rgb(195, 195, 195);
    font-size: 11px;
}